.media {
  display: flex;
  flex-direction: column;
  padding: 5px;
  margin: 15px auto;
  background-color: #282c34;
  border-radius: 10px;
  position: relative;
  font-family: "Montserrat", sans-serif;
}

.leftLogo {
  position:absolute;
  top: 15px;
  left: 10px;
  font-weight: bold;
  color: black;
  padding: 4px 5px;
  border-radius: 5px;
  background-color: #3a6ddb;
  font-size: 16px;
}

.rightLogo {
  position: absolute;
  top: 15px;
  right:10px;
  font-weight: bold;
  color: black;
  background-color: rgb(202, 102, 21);
  font-size: 16px;
  padding: 4px 5px;
  border-radius: 5px;
}

.videoTimeBox {
  display: inline-flex;
  position: relative;
  flex-shrink: 0;
  vertical-align: middle;
}

.videoTime {
  color: white;
  background-color: #444;
  font-size: 16px;
  bottom: 10px;
  right: 10px;
  padding: 5px 10px;
  border-radius: 5px;
  position: absolute;
}

.imageBox {
  background-color: black;
  text-align: center;
  height:300px;
  width: 100%;
  display: flex;
}

.poster {
  max-height: 100%;
  max-width: 100%;
  margin:auto;
}

.title {
  width: 100%;
  text-align: left;
  font-size: 17px;
  padding: 8px 3px;
  color: #FE8800;
  font-weight: bold;
}

.subTitle {
  display: flex;
  justify-content: space-between;
  padding-bottom: 3px;
  padding: 0 2px;
  padding-bottom: 3px;
}
