.buttonLine {
    position: relative;
    font-size: 16px;
    color: chocolate;
}

.myTopRightLink {
    position: absolute;
    top: -70px;
    right: 0;
    z-index: 110;
    font-weight: bold;
}

.myTopRightLink:hover {
    border-bottom: 1px solid chocolate;
}

.playTitle {
    color: #fe8800;
    font-size: 22px;
    font-weight: bold;
    margin: 10px 0 5px 0;
}

.playerBox {
    padding: 10px 5px;
    border-radius: 10px;
    background-color: #282c34;
}

.videoInfoLine {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 10pt;
    padding: 5px;
}

.videoInfoLeft {
    flex: 6;
}

.videoInfoRight {
    text-align: right;
    flex: 6;
}

.react-player__preview {
    background-repeat: no-repeat;
    background-size: contain !important;
}

.likeBox {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.likeBox > .media {
    width: 45%;
}

.likeBox > .media > .imageBox > .leftLogo {
    font-size: 8px;
}

.likeBox > .media > .imageBox > .rightLogo {
    font-size: 8px;
}

.likeBox > .media > .title {
    font-size: 16px;
}

.likeBox > .media > .subTitle {
    font-size: 14px;
}

.likeBoxTitle {
    color: chocolate;
    font-size: 16px;
    font-weight: bold;
    margin-top: 20px;
}
