.mypage {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.mypage > .media {
    width: 45%;
}

.mypage > .media > .imageBox > .leftLogo {
    font-size: 8px;
}

.mypage > .media > .imageBox > .rightLogo {
    font-size: 8px;
}

.mypage > .media > .title {
    font-size: 16px;
}

.mypage > .media > .subTitle {
    font-size: 14px;
}

.mypageTitle {
    font-size: 20px;
    font-weight: bold;
    color: rgb(226, 128, 58);
    padding-left:10px;
}
