.header {
  width: 100%;
  cursor: pointer;
  position: fixed;
  display: flex;
  text-transform: uppercase;
  background-color: #39445a;
  font-family: "Montserrat", sans-serif;
  box-shadow: 0px 1px 5px black;
  z-index: 100;
  padding: 10px 0;
  height: 43px;
  justify-content: space-between;
}

.topBarRightLink {
  font-size: 16px;
  font-weight: bold;
  color: chocolate;
  /* margin-right: 20px; */
  cursor: pointer;
  /* margin-top: 10px; */
  position: fixed;
  top:20px;
  right:20px;
}

.headerLogo {
  width: 150px;
}